import { debounce } from 'common/utils'
import BackTop from 'components/content/backTop/BackTop'
// mixin mountedmounted
// 
// 
export const itemImgListenerMixin = {
  data() {
    return {
      itemImgListener: null,
    }
  },
  mounted() {
    const refresh = debounce(this.$refs.scroll.refresh, 100)
    this.itemImgListener = () => {
      refresh()
    }
    this.$bus.$on('itemImageLoad', this.itemImgListener)
  },
}

// 
export const backTopMixin = {
  data() {
    return {
      isShowBackTop: false,
    }
  },
  components: {
    BackTop,
  },
  methods: {
    backTop() {
      this.$refs.scroll.scrollTo(0, 0)
    },
  },
}

// VUE/
// 
//
//   
// 1. websku
// 
// 2. APPwebviewAPPjswindowvuexskuShowtrueAPPskuShowfalseskuShowfalse

export const tabBarBackMixin = {
  mounted() {
    if (window.history && window.history.pushState) {
      history.pushState(null, null, document.URL)
      // false
      window.addEventListener('popstate', this.quitMessage, false)
    }
  },
  deactivated() {},
  destroyed() {
    console.log('app destroyed')
    // false
    window.removeEventListener('popstate', this.quitMessage, false)
  },
  methods: {
    quitMessage() {
      this.$toast.show('')
      setTimeout(() => {
        history.pushState(null, null, document.URL)
      }, 100)
    },
  },
}



